import Vue from 'vue'
import Router from 'vue-router'
// import Home from '@/components/Home'
// import qqlogin from '@/components/qqlogin'
//路由懒加载分组
// const Home = () => import(/* webpackChunkName: "Home" */'@/components/Home')
// const qqlogin = () => import(/* webpackChunkName: "QQlogin" */'@/components/qqlogin')

const Home =  resolve => require.ensure([], () => resolve(require('@/components/Home')),'HomeWebpack')
const qqlogin =  resolve => require.ensure([], () => resolve(require('@/components/qqlogin')),'QQloginWebpack')
Vue.use(Router)

export default new Router({
	mode:'history',
	routes: [{
			path: '/',
			name: 'Home',
			component: Home
		},
		{
			path: '/memo/qqlogin',
			name: 'qqlogin',
			component: qqlogin
		}
	]
})
